import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useParams } from "react-router";
import { Link } from "react-router-dom";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  useToast,
} from "@chakra-ui/react";
import { AiOutlineArrowLeft, AiOutlineQuestionCircle } from "react-icons/ai";
import { BiError, BiCheck } from "react-icons/bi";
import BorderedTable from "../../components/BorderedTable";
import { statusPill } from "../../common/ui/Pill";
import "../customer.css";
// import { getTokens, deleteTokens } from '../../apis/customer';
import { timestampToDate, snakeToTitleCase } from "../../common/utils";
import TokensMockData from "../../mockData/tokens";
import CardsMockData from "../../mockData/cards";
import {
  setTokensRecords,
  updateToken,
  deleteAllTokens,
} from "../../actions/tokens";

const AllTokens = () => {
  // const [tokens, setTokens] = useState(null);
  const [selectedToken, setSelectedToken] = useState(null);
  const [deleteAll, setDeleteAll] = useState(false);
  const params = useParams();
  const dispatch = useDispatch();
  const tokensAllRecords = useSelector((state) => state.tokens);
  const cardId = params?.card_data;
  const tokensRecords = tokensAllRecords[cardId];
  const { isOpen, onOpen, onClose } = useDisclosure();
  const toast = useToast();
  const cardDetails = CardsMockData.filter(
    (e) => e.redacted_card_number === cardId
  )[0];

  // console.log(params.card_data);

  // const fetchTokens = () => {
  //   // const card_id = params?.card_data.substring(4);
  //   // getTokens(card_id).then(({ data }) => {
  //   //   setTokens(data.data);
  //   // });
  // };

  useEffect(() => {
    if (Object.keys(tokensAllRecords).length === 0) {
      dispatch(setTokensRecords(TokensMockData));
    }
  }, []);

  const deleteToken = () => {
    if (!deleteAll) {
      // eslint-disable-next-line object-property-newline
      dispatch(
        updateToken({ status: "deleted", id: selectedToken.id, cardId })
      );
      toast({
        title: "Token Deleted Successfully",
        status: "success",
        isClosable: true,
        position: "top-right",
        duration: 3000,
      });
    } else {
      dispatch(deleteAllTokens({ cardId }));
      toast({
        title: "All Tokens Deleted Successfully",
        status: "success",
        isClosable: true,
        position: "top-right",
        duration: 3000,
      });
    }

    onClose();
    setDeleteAll(false);
    setSelectedToken(null);
  };

  const triggerDeleteAll = () => {
    setDeleteAll(true);
    onOpen();
  };

  const fields = [
    [
      "Merchant",
      (item) => (
        <div className="table-data heimdall">
          {item.status === "deleted" ? (
            <BiError className="red-error" />
          ) : (
            <BiCheck className="green-tick" />
          )}

          <div className="column">
            <span className="semi-bold">
              {snakeToTitleCase(item?.merchant)}
            </span>
            <span className="info-block">
              {item.status !== "active"
                ? `${item.status} at ${timestampToDate(item.updated_at)}`
                : `Created at ${item.created_at}`}
            </span>
          </div>
        </div>
      ),
    ],
    [
      "Status",
      (item) => <div className="table-data">{statusPill(item.status)}</div>,
    ],
    [
      "Token Service Provider",
      (item) => (
        <div className="table-data justify-center">
          <img
            src={item.service_provider_logo}
            alt={item.service_provider_name}
          />
        </div>
      ),
    ],
  ];

  return (
    <div>
      <div className="top-bar grey-strip">
        <div className="row-start">
          <Link to="/customer/cards">
            <AiOutlineArrowLeft />
          </Link>{" "}
          <span className="bold">
            XXXX XXXX XXXX {params?.card_data.substring(0, 4)}{" "}
            {statusPill("ACTIVE")}
          </span>
        </div>
        <div>
          <button className="btn btn-hide-filters" onClick={triggerDeleteAll}>
            Delete All Saved Cards
          </button>
        </div>
      </div>
      <div className="box">
        <div className="wrapper-prashant heimdall">
          <div>
            <div className="table-row detail-header table-row-flexed">
              <div className="table-data">Merchant</div>
              <div className="table-data">Status</div>
              <div className="table-data justify-center">
                Token Service Provider
              </div>
            </div>
            <div className="table-bordered">
              {tokensRecords && (
                <BorderedTable
                  items={tokensRecords}
                  fields={fields}
                  selectedRowId={selectedToken?.token_id}
                  onRowClick={setSelectedToken}
                />
              )}
            </div>
          </div>

          <div>
            {selectedToken ? (
              <div className="boogey">
                <div className="detail-container ">
                  <div className="heading bold row">
                    Token Details
                    <div
                      className="detail-header semi-bold"
                      onClick={() => {
                        setSelectedToken(null);
                      }}
                    >
                      X
                    </div>
                  </div>
                  <hr />
                  <div className="detail-data">
                    <div className="table-row">
                      <div className="table-data">Expiry</div>
                      <div className="table-data">
                        {selectedToken?.expiry_at}
                      </div>
                    </div>
                    <div className="table-row">
                      <div className="table-data">Token Service Provider</div>
                      <div className="table-data">
                        {selectedToken?.service_provider_name}
                      </div>
                    </div>
                    <div className="table-row">
                      <div className="table-data">Merchant</div>
                      <div className="table-data">
                        {snakeToTitleCase(selectedToken?.merchant)}
                      </div>
                    </div>
                    <div className="table-row">
                      <div className="table-data">Activity</div>
                      <div className="table-data">
                        {selectedToken?.status !== "active"
                          ? `${selectedToken?.status} at ${timestampToDate(
                              selectedToken?.updated_at
                            )}`
                          : `Created at ${selectedToken?.created_at}`}
                      </div>
                    </div>
                  </div>
                  {selectedToken.status !== "deleted" && (
                    <button
                      className="customer-btn btn-hide-filters occupy-width"
                      onClick={onOpen}
                    >
                      Delete Saved Card
                    </button>
                  )}
                </div>
              </div>
            ) : (
              <div className="boogey">
                <div className="detail-header">Card Details</div>
                <div className="token-info">
                  <div className="table-row">
                    <div className="table-data">Card</div>
                    <div className="table-data">
                      {cardDetails.network} {cardId}
                    </div>
                  </div>
                  <div className="table-row">
                    <div className="table-data">Saved Cards</div>
                    <div className="table-data">
                      {tokensRecords
                        ? tokensRecords.filter(
                            (obj) => obj.status !== "deleted"
                          ).length
                        : 0}
                    </div>
                  </div>
                  <div className="table-row">
                    <div className="table-data">Deleted Cards</div>
                    <div className="table-data">
                      {tokensRecords
                        ? tokensRecords.filter(
                            (obj) => obj.status === "deleted"
                          ).length
                        : 0}
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      <Modal
        isOpen={isOpen}
        onClose={() => {
          onClose();
          setDeleteAll(false);
        }}
        size="sm"
      >
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>
            Delete {deleteAll ? "all" : "this"} saved{" "}
            {deleteAll ? "cards" : "card"}?
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            This action cannot be undone. Are you sure you want to delete{" "}
            {deleteAll ? "all" : "this"} saved {deleteAll ? "cards" : "card"}?
          </ModalBody>

          <ModalFooter flexDirection="row" justifyContent="space-between">
            <button
              className="customer-btn btn-hide-filters equal-width"
              onClick={() => {
                onClose();
                setDeleteAll(false);
              }}
            >
              No
            </button>
            <button
              className="customer-btn btn-hide-filters equal-width fill"
              onClick={deleteToken}
            >
              Yes
            </button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </div>
  );
};

export default AllTokens;
